.badge {
  background-color: #592dea;
  color: #fff;
  font-size: 10px;
  font-weight: 200;
  padding: 2px 4px;
  margin-right: 4px;
  text-transform: uppercase;
}


.text {
  -webkit-align-items: baseline;
          align-items: baseline;
  display: -webkit-flex;
  display: flex;
}

.list-item {
  padding: 2px;
}

.list-subitem {
  font-size: 14px;
  padding: 4px 8px;
}

.list-subitems {
  padding: 0 16px;
}

.list {
  padding: 8px;
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.left-nav {
  background-color: #f9fafc;
  padding: 8px;
  min-width: 120px;
}

html,
body {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 16px;
  margin: 0;
  min-height: 100vh;
  padding: 0;
  color: #1f2d3d;
}

#root {
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4 {
  font-weight: 400;
  margin: 0;
  padding: 8px;
}

a {
  color: #1f2d3d;
  text-decoration: none;
}

a:hover,
a:focus {
  text-decoration: underline;
}

a:visited {
  color: #1f2d3d;
}

